/**
 *  --> 
 * @param name 
 * @return 
 */
const kebab2Camel = (name) => name.replace(/(?:^|-)([a-zA-Z0-9])/g, (m, $1) => $1.toUpperCase());

/**
 *  --> 
 * @param name 
 * @return 
 */
const Camel2kebab = (name) => name.replace(/([A-Z]|[0-9]+)/g, (m, $1, offset) => (offset ? '-' : '') + $1.toLowerCase());

export default {
    kebab2Camel,
    Camel2kebab,
};
